.add-list {
  &__popup {
    width: 240px;
    height: 150px;
    background: #ffffff;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 18px;
    position: relative;
    box-sizing: border-box;
    top: -15px;

    .button {
      width: 100%;
      margin-top: 15px;
    }
    &-close-btn {
      position: absolute;
      right: -8px;
      top: -8px;
      cursor: pointer;
    }
    &-colors {
      display: flex;
      justify-content: space-between;
      margin-top: 12px;
    }

    .badge {
      width: 20px;
      height: 20px;
      cursor: pointer;
      border: 2px solid transparent;
      &.active {
        border: 2px solid #525252;
      }
    }
  }
}
